.firstBlock {
    background-color: #0093E9;
    padding: 15;
    box-shadow: 0px -1px 13px 3px rgba(0, 0, 0, 0.2);
}
.firstBlock .container {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
}
.firstBlock .container .box {
    margin: 15px 0 !important;
}

.firstBlock .container .box .icon {
    color: white !important;
}
.peoples {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 72px;
    padding: 3.1px 15px 0 15px;
    transition: 0.4s;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none; 
    border-top: 1px solid #808080;
    border-bottom: 1px solid #808080;
}
.peoples:hover {
    transition: 0.4s;
    background-color: rgba(0, 147, 227, 0.4);
}
.secondBlock {
    min-width: -webkit-max-content;
    min-width: max-content;
    height: inherit;
    background-color: white;
    box-shadow: 0px -1px 3px rgba(0, 0, 0, 0.2);
    overflow: hidden;
}
.contacts {
    overflow-y: auto;
    height: inherit;
}
.contact-info {
    padding: 17.9px 30px;
    border-bottom: 1px solid #808080;
}
.contact-info .contact-info-left {
    flex-wrap: nowrap !important;
}

.contact-info .contact-avatar {
    width: 70px !important;
    height: 70px !important;
    margin-right: 25px;
}
.contact-info .contact-status {
    display: flex;
    align-items: center;
}

.contact-info .send-image input { display: none; }
.contact-info .send-image .contact-button {
    width: 36px !important;
    height: 36px !important;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.message {
    margin: 10px 0 !important;
}
.message .text {
    max-width: 17vw;
    width: -webkit-max-content;
    width: max-content;
    padding: 10px;
    position: relative;
    background-image: linear-gradient(160deg, #0505fc 0%, #8b00ff 100%);
    color: white;
    border-radius: 10px;
}
.message .image {
    border-radius: 5px;
    max-width: 20vw;
    max-height: 25vh;
    width: -webkit-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: max-content;
}
.thirty-block {
    background-color: white;
    border-left: 1px solid #808080;
    border-right: 1px solid #808080;
    min-width: 300px !important;
}

.zero-id { padding: 15px; }
.zero-id * { text-align: center; }
.contact-id {
    flex-wrap: nowrap !important;
    overflow: auto;
}

.chat {
    margin-bottom: 17.9px !important;
    padding: 0 30px !important;
    overflow-y: auto;
    overflow-x: hidden;
    height: inherit !important;
}

.send { 
    flex-wrap: nowrap !important;
    padding: 0 30px 5px;
}
.send-input {
    padding: 9px 12px 11px;
    padding-left: 20px;
    border: 1px solid #0093E9;
    border-right: 0;
    border-radius: 50px 0 0 50px;
    background-color: white;
}
.send-button {
    margin-left: -40px !important;
    background-color: #0093E9 !important;
    width: 62px !important;
    height: 62px !important;
    border-radius: 30px;
}
.send-icon {
    width: 36px !important;
    height: 36px !important;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    margin-left: 6px;
    margin-bottom: 5px;
    color: white;
}
.connection {
    padding: 0 30px;
}
.connection svg {
    width: 3vw;
    height: 3vw;
}
.main {
    margin: -8px;
    height: 100%;
    padding: 30px 15px;
}
html {
    height: 100%;
    background-color: #0093E9;
    background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
}
body,
#root {
    height: 100%;
}

::-webkit-scrollbar { width: 0.5vw; }
::-webkit-scrollbar-track { display: none; }
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-image: linear-gradient(160deg, #0505fc 0%, #8b00ff 100%);
}
